import request from './request'
// Ëé∑ÂèñÁî®Êà∑Ê≠åÂçï
export const getUserSongList = (userId) => {
  return request({
    url: '/user/playlist',
    params: {
      uid: userId,
      timestamp: new Date().getTime()
    }
  })
}

// ÂàõÂª∫Ê≠åÂçï
export const setCreateUserSongList = (name, cookie) => {
  return request({
    url: '/playlist/create',
    params: {
      name,
      cookie
    }
  })
}

// Ëé∑ÂèñÁî®Êà∑‰ø°ÊÅØ
export const getUserInfo = (cookie) => {
  return request({
    url: '/user/subcount',
    params: {
      cookie
    }
  })
}
